.main-container {
    display: flex;
    /* align-items: center; */
    align-items: stretch;
    height: 100vh;
}

.content {
    flex: 1;
    /* margin-top: 20px; */
    padding: 20px 20px 0 20px;
    /* margin-bottom: 80px; */
    overflow: auto;
   
}
.content-width {
    height: 100%;
    margin: 0 auto;
    max-width: 900px;
    margin-bottom: 100px;
}

.menu {
    width: 200px;
    white-space: nowrap;
    overflow: auto;
    text-overflow: ellipsis;
    /* padding-left: 20px; */
    padding-top: 20px;
    border-right: 1px solid #ccc;
}

.menu-header {
    margin-bottom: 8px;
    color: #93959e;
    font-size: 12px;
    padding-left: 20px;
}

.sub-item {
    padding: 10px 0;
    cursor: pointer;
    padding-left: 20px;
    font-size: 13px;
    display: block;
    text-decoration: none;
    color: #0f0f0f;
}
.sub-active:active {
    color: #0f0f0f;
}
.sub-item:hover {
    background-color: #f3f3f6;
    color: #0f0f0f;
}

.sub-active {
    background-color: #ebebf0;
}

.item-container {
    margin-bottom: 20px;
}
.center {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.loading {
    fill: #00b831;
}
::-webkit-scrollbar {
    width: 10px;
    height: 10px;
    background-color: #fff;
}
::-webkit-scrollbar-thumb {
    background-color: #e1e6eb;
    border: solid 2px #fff;
    border-radius: 6px;
}