.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.table {
  width: 100%;
  height: 400px;
}

.headerCell {
  color: #6c737b;
  display: flex;
  align-items: center;
  padding: 0 9px;
}

.cell {
  display: flex;
  align-items: center;
  padding: 0 9px;
  border-bottom: solid 1px #ebedf0;
}

.header {
  background-color: #f6f8fa;
}

.fixedClassName{
  position: absolute;
  z-index: 1;
  top: 0;
  background-color: #fff;
}
